import styled from "styled-components";
import posed from "react-pose";

export const Container = styled.div`
  :before {
    content: "";
    background-color: rgb(24, 119, 242);
    position: absolute;
    top: 0px;
    height: 5px;
    left: 0;
    right: 0;
    margin: 0px auto;
    padding: 0px;
    transform-origin: 0% 0%;
    animation: scaleTransform 0.5s;
    @keyframes scaleTransform {
      0% {
        transform: scaleX(0);
      }
      100% {
        transform: scaleX(1);
      }
    }
  }
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const StyledForm = styled.form`
  padding: 60px 20px 20px 20px;
  border-radius: 5px;
  transition: 0.5s;
  &.none {
    opacity: 0;
    transition: 0.5s;
    background: transparent;
    border: 1px solid rgba(0, 0, 0, 0);
    border-bottom: 2px solid rgba(0, 0, 0, 0);
  }
  &.fill {
    opacity: 1;
    transition: 0.5s 0.5s;
    background: rgba(255, 255, 255, 1);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 2px solid rgba(0, 0, 0, 0.3);
  }

  .form-group {
    display: flex;
    flex-direction: column;
    transition: 0.5s;
    .logo {
      display: flex;
      justify-content: center;
      position: absolute;
      top: 0;
      left: 0;
      transition: 0.5s;
    }
    svg {
      fill: rgb(24, 119, 242);
      height: 40px;
      width: 40px;
    }
    input {
      border: 1px solid rgba(0, 0, 0, 0.2);
      padding: 5px;
      margin-bottom: 15px;
    }
    .btn {
      padding: 5px;
      text-align: center;
      color: #fff;
      background: rgb(24, 119, 242);
      border: 1px solid rgba(0, 0, 0, 0.1);
      cursor: pointer;
      font-size: 14px;
      &.btn-primary {
        margin-bottom: 5px;
        color: #fff;
        background: rgb(24, 119, 242);
      }
      &.btn-default {
        color: #666;
        background: #eee;
      }
    }
  }
  a {
    svg {
      fill: rgb(24, 119, 242);
      height: 40px;
      width: 40px;
    }
  }
`;
export const Form = posed(StyledForm)({
  enter: {
    opacity: 1,
    delay: 500
  },
  exit: {
    opacity: 0
  },
  leave: {
    opacity: 0,
    translate: "transformY(15px)"
  }
});
