import React, { useState, useEffect } from "react";
import axios from "axios";
// import { NavLink } from "react-router-dom";
import { Transition, Header, Main, Footer } from "./styles";
import { motion } from "framer-motion";
import {
  IoMdMail,
  IoLogoLinkedin,
  // IoIosFlask,
  IoMdArrowDroprightCircle
} from "react-icons/io";

export default ({ history }) => {
  const [items, setItems] = useState([]);
  // const [customOpacity, setOpacity] = useState(false);

  useEffect(() => {
    async function getData() {
      const res = await axios.get(
        "https://gist.githubusercontent.com/rodrigoant/f906faf135145c885f3bcc4e1c944571/raw/"
      );
      setItems(res.data);
    }
    getData();
  }, []);

  // const goToLab = e => {
  //   const xPos = e.target.getBoundingClientRect().x;
  //   const yPos = e.target.getBoundingClientRect().y;
  //   setOpacity(true);

  //   setTimeout(() => {
  //     history.push("/lab/", {
  //       x: xPos,
  //       y: yPos
  //     });
  //   }, 1300);
  // };

  return (
    <>
      <Transition />
      <Header
        // className={customOpacity && "exiting"}
        initialPose="exit"
        pose="enter"
      >
        <motion.h1
          initial={{ clipPath: "inset(0 100% 0 0)" }}
          animate={{ clipPath: "inset(0 0 0 0)" }}
          transition={{ delay: 0, duration: 1 }}
        >
          <span>
            <strong>Olá</strong>, meu nome é <strong>Rodrigo Antunes</strong>
          </span>
        </motion.h1>
        <motion.h3
          initial={{ clipPath: "inset(0 100% 0 0)" }}
          animate={{ clipPath: "inset(0 0 0 0)" }}
          transition={{ delay: 0.3, duration: 1 }}
        >
          <span>Desenvolvedor Front-End</span>
        </motion.h3>
        <motion.div
          className="description"
          initial={{ clipPath: "inset(0 100% 0 0)" }}
          animate={{ clipPath: "inset(0 0 0 0)" }}
          transition={{ delay: 0.5, duration: 1 }}
        >
          <span>
            <p>
              Trabalho há {new Date().getFullYear() - 2009} anos com
              Desenvolvimento Web.
            </p>
            <p>
              Gosto de criar experiências interativas com HTML, CSS e Javascript
            </p>
          </span>
        </motion.div>
        <div className="contato">
          <ul>
            <motion.li
              initial={{ transform: "translateY(100%)" }}
              animate={{ transform: "translateY(0%)" }}
              transition={{ delay: 1.5 }}
              duration="0.5"
            >
              <a
                target="_blank"
                rel="noopener noreferrer"
                href="https://www.linkedin.com/in/rodrigoantunespaiva/"
              >
                <IoLogoLinkedin className={"icon-logo"} />
              </a>
            </motion.li>
            <motion.li
              initial={{ transform: "translateY(100%)" }}
              animate={{ transform: "translateY(0%)" }}
              transition={{ delay: 1.75 }}
              duration="0.5"
            >
              <a
                target="_blank"
                rel="noopener noreferrer"
                href="mailto:contatorodrigoantunes@gmail.com"
              >
                <IoMdMail className={"icon-logo"} />
              </a>
            </motion.li>
            {/* <motion.li
              // style={{ display: "none" }}
              initial={{ transform: "translateY(100%)" }}
              animate={{ transform: "translateY(0%)" }}
              transition={{ delay: 2 }}
              duration="0.5"
              onClick={goToLab}
            >
              <IoIosFlask className={"icon-logo"} />
            </motion.li> */}
          </ul>
        </div>
      </Header>
      <Main
      // className={customOpacity && "exiting"}
      >
        {items.map(item => (
          <section
            key={item.id}
            style={{
              backgroundColor:
                item.id % 2 !== 0 ? "rgb(250,250,250)" : "rgb(240,240,240)"
            }}
          >
            <motion.div
              className="container"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ delay: 2 }}
            >
              <div className="left-container">
                <a
                  title={item.name}
                  target="_blank"
                  rel="noopener noreferrer"
                  href={item.link}
                >
                  <img src={item.image} alt={item.name} />
                </a>
              </div>
              <div className="right-container">
                <div className="client-name">{item.name}</div>
                <div className="client-info">{item.job}</div>
                <div className="client-desc">{item.description}</div>
                <div className="client-link">
                  <a
                    title={item.name}
                    target="_blank"
                    rel="noopener noreferrer"
                    href={item.link}
                  >
                    <IoMdArrowDroprightCircle />
                    Acesse o projeto
                  </a>
                </div>
              </div>
            </motion.div>
          </section>
        ))}
      </Main>
      <Footer initialPose="exit" pose="enter">
        {new Date().getFullYear()} | Rodrigo Antunes
      </Footer>
    </>
  );
};
