import React, { useState, useEffect } from "react";
import { Container, Form } from "./styles";
import { IoIosFlask } from "react-icons/io";
import useWindow from "../../hooks/useWindow";

export default ({ history, location }) => {
  const { width, height } = useWindow();
  const [name, setName] = useState("");
  const [change, setChange] = useState(true);

  let x;
  let y;

  if (location.state) {
    x = location.state.x;
    y = location.state.y;
  } else {
    x = 0;
    y = 0;
  }

  const centerX = width / 2 - 20;
  const centerY = height / 2 - 110;

  useEffect(() => {
    setTimeout(() => {
      setChange(false);
    }, 0);
  }, []);

  const handleSubmit = e => {
    e.preventDefault();
    history.push("/main", name);
  };
  const goToHome = e => {
    e.preventDefault();
    history.push("/");
  };

  const startAnimation = {
    transform: `translate(${x}px, ${y}px)`
  };
  const finishedAnimation = {
    transform: `translate(${centerX}px, ${centerY}px)`
  };

  return (
    <Container>
      <Form
        className={change ? "none" : "fill"}
        pose="enter"
        onSubmit={handleSubmit}
      >
        <div className="form-group">
          <div
            className="logo"
            style={change ? startAnimation : finishedAnimation}
          >
            <IoIosFlask className={"icon-logo"} />
          </div>
        </div>
        <div className={`form-group ${change ? "none" : "fill"}`}>
          <input
            value={name}
            placeholder="Seu nome"
            required
            type="text"
            name=""
            id=""
            onChange={e => setName(e.target.value)}
          />
          <input type="text" value="******" readOnly id="" />
          <button className="btn btn-primary" type="text" name="" id="">
            Login
          </button>
          <div
            onClick={goToHome}
            className="btn btn-default"
            type="text"
            name=""
            id=""
          >
            Voltar
          </div>
        </div>
      </Form>
    </Container>
  );
};
