import styled from "styled-components";

export const Container = styled.div`
  :before {
    content: "";
    background-color: rgb(24, 119, 242);
    position: absolute;
    top: 0px;
    height: 5px;
    left: 0;
    right: 0;
    margin: 0px auto;
    padding: 0px;
    transform-origin: 0% 0%;
    transform: scaleX(1);
  }
  padding: 25px;
  .backButton {
    padding: 10px 0;
    a {
      text-decoration: none;
      color: #666;
    }
  }
  .title {
    margin: 10px 0 30px 0;
    display: block;
  }
  .items {
    display: flex;
    .item {
      margin: 0 5px 0 0;
    }
  }
`;
