import React, { useEffect } from "react";
import { NavLink } from "react-router-dom";
import { motion } from "framer-motion";
import { Container } from "./styles";

const Main = ({ history }) => {
  useEffect(() => {
    if (!history.location.state) {
      return history.push("/");
    }
  }, [history]);

  return (
    <Container>
      <div className="backButton">
        <NavLink to="/">Voltar ao portfolio</NavLink>
      </div>
      <motion.div
        className="title"
        initial={{ clipPath: "inset(0 100% 0 0)" }}
        animate={{ clipPath: "inset(0 0 0 0)" }}
      >
        <h1>Olá, {history.location.state}, tenho projetos 2020 aqui:</h1>
      </motion.div>
      <motion.div
        drag="x"
        dragConstraints={{ left: 0, right: 0 }}
        className="items"
      >
        <div className="item">
          <img alt="teste" src={"https://placehold.it/200x250"} />
        </div>
        <div className="item">
          <img alt="teste" src={"https://placehold.it/200x250"} />
        </div>
        <div className="item">
          <img alt="teste" src={"https://placehold.it/200x250"} />
        </div>
      </motion.div>
    </Container>
  );
};

export default Main;
